.cirkle-loader {
    height: 20px;
    width: 20px;
    position: relative;
    div {
        position: absolute;
        border: 2px solid;
        border-radius: 50%;
        border-color: transparent #fff transparent transparent;
        width: 100%;
        height: 100%;
        animation: spinning 0.75s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    }

    :nth-child(2) {
        animation-delay: 0.07s;
    }
    :nth-child(3) {
        animation-delay: 0.14s;
    }
    &--big{
        height: 70px;
        width: 70px;
    }
    &--blue{
        div{
            border-color: transparent #3484f0 transparent transparent;
        }
    }
    &--center{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

@keyframes spinning {
    0% {
        transform: rotate(0deg);
    }
    85% {
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
